import { useContext } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { LoginAuthContext } from "Context/Authentication/LoginAuth";
import Logo from "./Components/Logo";
import BrowseMenu from "./Components/BrowseMenu";
import SearchBar from "./Components/SearchBar";
import AuthButtons from "./Components/AuthButtons";
import UserMenu from "./Components/UserMenu";
import LanguageSwitcher from "Components/Button/LanguageSwitcher";

const DesktopNav = () => {
  const { isAuthenticated } = useContext(LoginAuthContext);

  return (
    <div className="hidden xl:flex items-center gap-x-6 w-full">
      {/* Logo */}
      <Logo />

      {/* Navigation  Browse mega-menu + links (unauthenticated) OR inline links (authenticated) */}
      {isAuthenticated ? (
        <AuthenticatedLinks />
      ) : (
        <BrowseMenu />
      )}

      {/* Right side: search icon + auth/user + language */}
      <div className="flex items-center gap-x-4 ms-auto">
        <SearchBar />
        {isAuthenticated ? <UserMenu /> : <AuthButtons />}
        <LanguageSwitcher />
      </div>
    </div>
  );
};

const AuthenticatedLinks = () => {
  const { t } = useTranslation();

  return (
    <nav className="flex items-center gap-x-6">
      <Link
        to="/categories"
        state={{ isAuthenticated: true }}
        className="text-white font-medium text-lg capitalize hover:text-primary transition-colors whitespace-nowrap"
      >
        {t("navigation.browse")}
      </Link>
      <Link
        to="/my-courses"
        className="text-white font-medium text-lg capitalize hover:text-primary transition-colors whitespace-nowrap"
      >
        {t("navigation.my_courses")}
      </Link>
    </nav>
  );
};

export default DesktopNav;
